import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    // index
    fetchVisitas(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-social/asistencia-social/visita', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // show
    fetchVisita(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-social/asistencia-social/visita/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // store
    addVisita(ctx, visitaData) {
      return new Promise((resolve, reject) => {
        axios
          .post('/servicio-social/asistencia-social/visita', visitaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // update
    updatedVisita(ctx, visitaData) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-social/asistencia-social/visita/${visitaData.id}`, visitaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // delete
    deleteVisita(ctx, visitaData) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-social/asistencia-social/visita/${visitaData.id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
